<script>
import {Table, TableColumn} from 'element-ui'
import axios from "axios";
import {baseUrl} from "../../../config/index.json";

export default {
  name: 'light-table',
  components: {
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      menus:[],
      rows: 1,
      perPage: 1,
      currentPage: 1,
      index: 0,
      right_t: false,
      data_main: [],
      title:'',
      titleru:'',
      titleen:'',
      order:'',
      isFooter:false,
      url:'',
      isButton:true,
      main_id:'',
      address:'',
      email_address:'',
      phone_number:'',
      telegram:'',
      instagram:'',
      facebook:'',
      contact:[],
      imageUrl:[],
      myImages:[],
      files_name:[],
      imageUrl1:[],
      myImages1:[],
      files_name1:[],
      imageUrl2:[],
      myImages2:[],
      files_name2:[],
      description:'',
      descriptionru:'',
      descriptionen:'',
      poster_description:"",
      poster_descriptionru:'',
      poster_descriptionen:'',
      poster_title:'',
      poster_titleru:'',
      poster_titleen:'',
      short_description:'',
      short_descriptionru:'',
      short_descriptionen:'',
    };
  },
  methods: {
    trySubmitFile(e) {
      this.imageUrl=[]
      this.myImages=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    trySubmitFile1(e) {
      this.imageUrl1=[]
      this.myImages1=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name1.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages1.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl1.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    trySubmitFile2(e) {
      this.imageUrl2=[]
      this.myImages2=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name2.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages2.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl2.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },

    rigt_side() {
      this.right_t = !this.right_t
      this.isButton=false
    },
    updatePage(item) {
      this.data_main = Users[item - 1]
    },
    createOz(){
      const form = new FormData();
      if(this.myImages.length==0&&this.myImages1.length==0&&this.myImages2.length==0){

      form.append("description", this.description);
      form.append("short_description", this.short_description);
      form.append("poster_title", this.poster_title);
      form.append("poster_description", this.poster_description);
      }
      else {
        form.append("logo", this.myImages[0]);
        form.append("image", this.myImages1[0]);
        form.append("file", this.myImages2[0]);
      form.append("description", this.description);
      form.append("short_description", this.short_description);
      form.append("poster_title", this.poster_title);
      form.append("poster_description", this.poster_description);

      }
      axios.patch(`${baseUrl}company-info/${this.$route.params.id}/`,form,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language':'oz'
          }
        })
        .then(res=>{

          this.$toast("Ma'lumotlar o'zbek tilida kiritildi", {
            timeout: 2000,
            type:"success"
          });
          //ru

          form.append("description", this.descriptionru);
          form.append("short_description", this.short_descriptionru);
          form.append("poster_title", this.poster_titleru);
          form.append("poster_description", this.poster_descriptionru);
          axios.patch(`${baseUrl}company-info/${this.$route.params.id}/`,form,
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`,
                'Accept-Language':'ru'
              }
            })
            .then(()=>{

              this.$toast("Ma'lumotlar rus tilida kiritildi", {
                timeout: 2000,
                type:"success"
              });
              //en

              form.append("description", this.descriptionen);
              form.append("short_description", this.short_descriptionen);
              form.append("poster_title", this.poster_titleen);
              form.append("poster_description", this.poster_descriptionen);
              axios.patch(`${baseUrl}company-info/${this.$route.params.id}/`,form,
                {
                  headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Accept-Language':'kar'
                  }
                })
                .then(()=>{

                  this.$toast("Ma'lumotlar barcha tillarda kiritildi", {
                    timeout: 2000,
                    type:"success"
                  });
                })
            })
        })
    },

  },

  mounted() {
    axios.get(`${baseUrl}company-info/${this.$route.params.id}`,{
      headers:{
        'Accept-Language':'oz'
      }
    })
      .then(res=>{
        this.menus=res.data
        this.imageUrl.push(this.menus.logo)
        this.imageUrl1.push(this.menus.image)
        this.imageUrl2.push({
          "name":this.menus.file
        })
        console.log(this.menus.logo)
        this.title=this.menus.title
