        this.poster_descriptionru=this.menus.poster_description
        this.short_descriptionru=this.menus.short_description
        console.log(res)
      })
    axios.get(`${baseUrl}company-info/${this.$route.params.id}`,{
      headers:{
        'Accept-Language':'kar'
      }
    })
      .then(res=>{
        this.menus=res.data
        this.poster_titleen=this.menus.poster_title
        this.poster_descriptionen=this.menus.poster_description
        this.short_descriptionen=this.menus.short_description
        this.descriptionen=this.menus.description
        this.titleen=this.menus.title
        console.log(res)
      })
  }
